 display: -webkit-inline-box;\n    display: inline-flex;\n    position: relative;\n    vertical-align: middle;\n    height: 418px;\n    width: 418px;\n}\n\n.legend-wheel-circle {\n    display: inherit;\n    position: inherit;\n    vertical-align: inherit;\n    height: inherit;\n    width: inherit;\n}\n\n.legend-wheel-img {\n    height: 100%;\n    width: auto;\n    z-index: 1;\n    -webkit-transform: rotate(0);\n    transform: rotate(0);\n    -webkit-transition: -webkit-transform 4.5s cubic-bezier(0.46, 0.07, 0.04, 0.98);\n    transition: transform 4.5s cubic-bezier(0.46, 0.07, 0.04, 0.98), -webkit-transform 4.5s cubic-bezier(0.46, 0.07, 0.04, 0.98);\n    will-change: opacity, transform;\n}\n\n.legend-wheel-img,\n.legend-wheel-inset {\n    display: inline-block;\n    position: absolute;\n}\n\n.legend-wheel-inset {\n    border-radius: 50%;\n    left: 0;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    z-index: 2;\n    border: 3px solid #3333338f;\n}\n\n.legend-wheel-arrow {\n    background: url(\"~@img/wheel/arrow.png\") 50% no-repeat;\n    position: absolute;\n    right: -40px;\n    top: 50%;\n    -webkit-transform: translateY(-50%);\n    transform: translateY(-50%);\n    width: 72px;\n    height: 100%;\n    z-index: 5;\n}\n\n.legend-wheel-fade-enter-active,\n.legend-wheel-fade-leave-active {\n    -webkit-transition: opacity 0.15s ease-in-out;\n    transition: opacity 0.15s ease-in-out;\n}\n\n.legend-wheel-coefficient-enter,\n.legend-wheel-coefficient-leave-to,\n.legend-wheel-fade-enter,\n.legend-wheel-fade-leave-to,\n.legend-wheel-winner-enter,\n.legend-wheel-winner-leave-to {\n    opacity: 0;\n}\n\n.legend-wheel-inner {\n    -webkit-box-align: center;\n    align-items: center;\n    background-color: #fff;\n    border: 6px solid #E1B5FF;\n    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.2), 0 0 32px 0 rgba(0, 0, 0, 0.65);\n    border-radius: 50%;\n    display: -webkit-box;\n    display: flex;\n    -webkit-box-pack: center;\n    justify-content: center;\n    position: absolute;\n    left: 50%;\n    top: 50%;\n    -webkit-transform: translate(-50%, -50%);\n    transform: translate(-50%, -50%);\n    width: 238px;\n    height: 238px;\n    z-index: 3;\n}\n\n.legend-wheel-coefficient,\n.legend-wheel-winner {\n    border-radius: 50%;\n    position: absolute;\n    will-change: opacity;\n}\n\n.legend-wheel-coefficient {\n    -webkit-box-align: center;\n    align-items: center;\n    background-color: #fff;\n    min-height: 50%;\n    min-width: 50%;\n    max-width: 50%;\n    backface-visibility: hidden;\n    -webkit-backface-visibility: hidden;\n    color: #000;\n}\n\n.legend-wheel-coefficient,\n.legend-wheel-coefficient-box {\n    flex: none;\n    display: -webkit-box;\n    display: flex;\n    -webkit-box-pack: center;\n    justify-content: center;\n    max-height: 50%;\n}\n\n.legend-wheel-coefficient-box,\n.legend-wheel-coefficient-list {\n    -webkit-box-align: start;\n    align-items: flex-start;\n}\n\n.legend-wheel-coefficient-list {\n    display: -webkit-box;\n    display: flex;\n    flex-direction: column;\n    -webkit-box-pack: start;\n    justify-content: flex-start;\n    margin-left: 24px;\n}\n\n.legend-wheel-coefficient-list:first-child {\n    margin-left: 0;\n}\n\n.legend-wheel-coefficient-item {\n    -webkit-box-align: center;\n    align-items: center;\n    display: -webkit-box;\n    display: flex;\n    -webkit-box-pack: center;\n    justify-content: center;\n    margin-bottom: 12px;\n}\n\n.legend-wheel-coefficient-item:last-child {\n    margin-bottom: 0;\n}\n\n.legend-wheel-coefficient-item > span {\n    color: #000;\n    font-weight: 500;\n    font-stretch: normal;\n    font-style: normal;\n    font-size: 18px;\n    margin-left: 7px;\n    letter-spacing: normal;\n    text-transform: uppercase;\n}\n\n.legend-wheel-coefficient-enter-active,\n.legend-wheel-coefficient-leave-active,\n.legend-wheel-winner-enter-active {\n    -webkit-transition: opacity 0.3s ease-in-out;\n    transition: opacity 0.3s ease-in-out;\n}\n\n.legend-wheel-winner {\n    background-color: #5669a1;\n    flex-direction: column;\n    min-height: 50%;\n    min-width: 50%;\n    max-width: 50%;\n    max-height: 50%;\n    backface-visibility: hidden;\n    -webkit-backface-visibility: hidden;\n}\n\n.legend-wheel-winner,\n.legend-wheel-winner-coefficient {\n    -webkit-box-align: center;\n    align-items: center;\n    display: -webkit-box;\n    display: flex;\n    -webkit-box-pack: center;\n    justify-content: center;\n}\n\n.legend-wheel-winner-coefficient {\n    background-color: #fde907;\n    border: 1px solid rgba(58, 42, 86, 0.3);\n    border-radius: 16px;\n    color: #3f2652;\n    margin-top: 7px;\n    width: 87px;\n    text-transform: uppercase;\n}\n\n.legend-wheel-prize-decimal,\n.legend-wheel-prize-integer,\n.legend-wheel-winner-box-shadow-orange .legend-wheel-winner-coefficient,\n.legend-wheel-winner-box-shadow-purple .legend-wheel-winner-coefficient,\n.legend-wheel-winner-box-shadow-red .legend-wheel-winner-coefficient {\n    color: var(--color-text);\n}\n\n.legend-wheel-prize-integer {\n    font-size: 26px;\n    line-height: 1.5;\n}\n\n.legend-wheel-prize-decimal {\n    font-size: 26px;\n    line-height: 1.23;\n    white-space: nowrap;\n}\n\n.legend-wheel-winner-leave-active {\n    -webkit-transition: opacity 0 ease-in-out;\n    transition: opacity 0 ease-in-out;\n}\n\n@media (max-width: 1024px) {\n    .page-wheel {\n        &__content {\n            flex-direction: column;\n            padding: 16px;\n        }\n\n        &__form {\n            max-width: 100%;\n            order: 2;\n        }\n        .form__group {\n            &:nth-child(1) {\n                order: 1;\n            }\n            &:nth-child(2) {\n                order: 2;\n                margin-bottom: 24px;\n            }\n        }\n        .button_primary {\n            order: 3;\n        }\n        &__circle {\n            margin: 0;\n            order: 1;\n        }\n    }\n\n    .legend-wheel {\n        width: 265px;\n        height: 265px;\n    }\n\n    .legend-wheel-inner {\n        width: 160px;\n        height: 160px;\n    }\n\n    .legend-wheel-coefficient-list {\n        margin-left: 10px;\n    }\n    .legend-wheel-coefficient-item {\n        margin-bottom: 8px;\n    }\n    .legend-wheel-coefficient-item > svg {\n        width: 12px;\n    }\n    .legend-wheel-winner-coefficient {\n        font-size: 14px;\n        width: 61px;\n    }\n}\n\n@media (max-width: 768px) {\n    .legend-wheel-arrow {\n        background-size: 70%;\n        right: auto;\n        top: 4px;\n        left: 50%;\n        height: 100px;\n        -webkit-transform: translate(-50%) rotate(-90deg);\n        transform: translate(-50%) rotate(90deg);\n    }\n\n    .legend-wheel-winner-coefficient {\n        margin-top: 2px;\n    }\n    .legend-wheel-prize-decimal,\n    .legend-wheel-prize-integer {\n        font-size: 20px;\n    }\n}\n\n@media (max-width: 414px) {\n    .legend-wheel-coefficient {\n        min-height: 56%;\n        min-width: 56%;\n        max-width: 56%;\n        max-height: 56%;\n    }\n    .legend-wheel-winner {\n        min-height: 54%;\n        min-width: 54%;\n        max-width: 54%;\n        max-height: 54%;\n    }\n}\n\n@media (max-width: 330px) {\n    .legend-wheel-coefficient,\n    .legend-wheel-winner {\n        min-height: 60%;\n        min-width: 60%;\n        max-width: 60%;\n        max-height: 60%;\n    }\n}\n@media (max-width: 900px) {\n  .form__buttons.bet {\n    display: none !important;\n  }\n}\n"],"sourceRoot":""}]);
